.page {
  height: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-feature-settings: "pnum" on, "lnum" on;
  font-size: 17px;
  line-height: 1.65;
  -webkit-text-size-adjust: 100%;
  color: var(--color-black);
  background-color: var(--color-white);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.site-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.fixed {
  position: relative;
  overflow: hidden;
}
.fixed::before {
  content: "";
  position: absolute;
  z-index: 5;
  width: 100%;
  height: 100vh;
  background-color: var(--color-black);
  opacity: 0.3;
}

.input-reset {
  border-color: transparent;
  padding: 15px 30px;
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.6;
  background-color: var(--color-noactive);
}
.input-reset::placeholder {
  color: var(--color-gray);
}

.main {
  padding-top: 72px;
}

.logo {
  width: 100%;
  max-width: 123px;
}

.slogan {
  display: block;
  margin-bottom: 14px;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.37;
  letter-spacing: -0.019em;
  text-transform: capitalize;
  color: var(--secondary-500);
}
@media (max-width: calc(29.75em - 0.0625em)) {
  .slogan {
    margin-bottom: 7px;
  }
}

.title {
  font-weight: 400;
  font-size: 40px;
  line-height: 1.1;
  letter-spacing: -1px;
  text-align: center;
}
.title span {
  font-weight: 700;
}
@media (max-width: calc(29.75em - 0.0625em)) {
