.footer {
  clip-path: polygon(0 9.5%, 100% 0, 100% 100%, 0 100%);
  margin-top: auto;
  padding: rem(120) 0 rem(60);
  font-size: 13px;
  line-height: 1.6;
  color: var(--neutral-800);
  background-color: var(--neutral-25);

  .container {
    @include for-width(-mobile-sl) {
      padding: 0 24px;
    }
  }

  p {
    &:last-of-type {
      margin-bottom: 23px;
    }
  }

  @include for-width(-mobile-sl) {
    clip-path: polygon(0 4%, 100% 0, 100% 100%, 0 100%);
    padding: 94px 0 80px;
    font-size: 15px;
  }
}
